.playoff-table * {
  box-sizing: border-box;
}

.playoff-table {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
  line-height: 1.42857143;
  font-weight: 400;
  color: rgb(80, 100, 100);
  width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  background-color: #d3d3e0;

}

.board {
  margin-left: 100px;
  margin-top: 0px;
}

.button-margin {
  margin-top: 4px;
}

.playoff-table .playoff-table-content {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: -ms-flex;
  display: flex;
  padding: 20px;
}

.playoff-table .playoff-table-tour {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: -ms-flex;
  display: flex;
  -webkit-align-items: center;
  -ms-align-items: center;
  align-items: center;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: space-around;
  -ms-justify-content: space-around;
  justify-content: space-around;
  position: relative;
}

.playoff-table .playoff-table-pair {
  position: relative;
}

.playoff-table .playoff-table-pair:before {
  content: "";
  position: absolute;
  top: 27px;
  right: -12px;
  width: 12px;
  height: 1px;
  background-color: blue;
}

.playoff-table .playoff-table-pair:after {
  content: "";
  position: absolute;
  width: 3px;
  height: 1000px;
  background-color: #d3d3e0;
  right: -12px;
  z-index: 1;
}

.playoff-table .playoff-table-pair:nth-child(even):after {
  top: 28px;
}

.playoff-table .playoff-table-pair:nth-child(odd):after {
  bottom: 28px;
}

.playoff-table .playoff-table-pair-style {
  border-radius: 10px;
  border: 1px solid #cccccc;
  background-color: rgb(233, 231, 231);
  width: 160px;
  margin-bottom: 20px;
}

.playoff-table .playoff-table-group {
  padding-right: 11px;
  padding-left: 10px;
  margin-bottom: 20px;
  position: relative;
  overflow: hidden;
  height: 100%;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: -ms-flex;
  display: flex;
  -webkit-align-items: center;
  -ms-align-items: center;
  align-items: center;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: space-around;
  -ms-justify-content: space-around;
  justify-content: space-around;
}

.playoff-table .playoff-table-group .playoff-table-pair-style:last-child {
  margin-bottom: 0px;
}

.playoff-table .playoff-table-group1 .playoff-table-pair-style:last-child {
  margin-left: 10px;
}

.playoff-table .playoff-table-group:after {
  content: "";
  position: absolute;
  top: 29px;
  bottom: 29px;
  right: 0px;
  width: 1px;
  background-color: blue;
}



.playoff-table .playoff-table-semi:after {
  content: "";
  position: absolute;
  top: 2px;
  bottom: 2px;
  right: 0px;
  width: -10px;
  background-color: white;
}

.playoff-table .playoff-table-group .playoff-table-final:after {
  content: "";
  position: absolute;
  top: 0px;
  bottom: 0px;
  right: 0px;
  width: 0px;
  background-color: blue;
}

.playoff-table .playoff-table-group:last-child {
  margin-bottom: 0;
}

.playoff-table .playoff-table-left-player,
.playoff-table .playoff-table-right-player {
  min-height: 26px;
  padding: 3px 5px;
}

.playoff-table .playoff-table-champ-player {
  min-height: 44px;
  padding: 16px 16px;
  border-bottom: 0px solid #cccccc;
  margin-top: 12px;
  margin-bottom: 10px;
  border-top: 0px solid #cccccc;
}

.winner {
  border-radius: 5px;
  background-color: rgb(89, 190, 89);
  font-weight: bold;
}

.playoff-table .playoff-table-left-player {
  border-bottom: 1px solid #cccccc;
}

.playoff-table .playoff-table-left-player-final {
  border-left: 1px solid #cccccc;
}

.playoff-table .playoff-table-left-player:before {
  content: "";
  position: absolute;
  bottom: 27px;
  left: -12px;
  width: 12px;
  height: 1px;
  background-color: blue;
}

.playoff-table .playoff-table-right-player {
  margin-top: -1px;
  border-top: 1px solid #cccccc;
}

.playoff-table .playoff-table-third-place {
  position: absolute;
  left: 1px;
  top: 50%;
  transform: translateY(-50%);
  margin-top: 100px;
}

.playoff-table .playoff-table-third-place .playoff-table-left-player:before {
  display: none;
}

.playoff-table .playoff-table-tour:first-child .playoff-table-group {
  padding-left: 0;
}

.playoff-table .playoff-table-tour:first-child .playoff-table-left-player:before {
  display: none;
}

.playoff-table .playoff-table-tour:last-child .playoff-table-group:after {
  display: none;
}

.playoff-table .playoff-table-tour:last-child .playoff-table-pair:after,
.playoff-table .playoff-table-tour:last-child .playoff-table-pair:before {
  display: none;
}

